import React from 'react'
import { Element } from 'react-scroll'
import { Grid } from 'react-flexbox-grid'
import Title from '../../common/title/title.component'
import MyForm from './contact-form/contact-form.component'
import TextAndImage from '../../common/text-and-image/text-and-image.component'

const Contact = () => {
  return (
    <Grid className='p-b-section'>
      <Element className='text-center' name='contact'>
      <article className='p-y-sm-section'>
        <Title>Contact</Title>
        <TextAndImage reverseMobile src='images/people/walk.jpg' alt='contact'>
          <MyForm className='text-left'/>
        </TextAndImage>
        </article> 
      </Element>
    </Grid>
  )
}

export default Contact
