import React from 'react'
import { Element } from 'react-scroll'
import { Grid } from 'react-flexbox-grid'
import Title from '../../common/title/title.component'
import SubTitle from '../../common/subtitle/subtitle.component'
import SectionText from '../../common/section-text/section-text.component'
import Skills from '../skills/skills.component'
import TextAndImage from '../../common/text-and-image/text-and-image.component'
import TextAndVideo from '../../common/text-and-video/text-and-video.component'
import Link from '../../common/link/link.component'

const About = () => {
  return (
    <Grid className='p-b-section fade-in'>
      <Element className='text-center' name='about'>
      <article className='p-y-sm-section'>
        <Title>About</Title>
        <TextAndImage src='images/people/design.jpg' alt='skills'>
          <SubTitle>Skills</SubTitle>
          <Skills />
        </TextAndImage>
        </article>
        <article className='p-y-sm-section'>
          <SubTitle>What I can do</SubTitle>
          <section className='p-y-sm-section'>
            <TextAndVideo left src='images/work/jachty-pruszynski.mp4' imgSrc='images/work/jachty-pruszynski.jpg' cClass='shadow-image'>
              <SectionText title='Website' >I can build your website in Wordpress, React or just Javascript. Having a website provides great opportunities for your business. This is a website for <Link outsideLink href='https://jachtypruszynski.pl/' cClass='section-link'>Jachty Pruszyński</Link> which I made.</SectionText>
            </TextAndVideo>
          </section>
          <section className='p-y-sm-section'>
            <TextAndImage reverseMobile src='images/work/desktop-mobile.jpg' alt='design'>
              <SectionText title='Design & Graphic'>I can create designs that look awesome on any device and prepare promotional graphic and image materials. This is <Link outsideLink href='https://ilshae.github.io/crwn-clothing/' cClass='section-link'>Crwn Clothing</Link>. It's a shop page with stripe payments.</SectionText>
            </TextAndImage>
          </section>
          <section className='p-y-sm-section'>
            <TextAndImage left src='images/work/seo.jpg' alt='seo' cClass='shadow-image'>
              <SectionText title='Hosting Support & SEO'>I can take care of your website. Optimise page structure and code, use tools like Google Analytics or Speed Insights to increase search results.</SectionText>
            </TextAndImage>
          </section>
          <section className='p-y-sm-section'>
            <TextAndImage reverseMobile src='images/work/e-commerce.jpg' alt='e-commerce'>
              <SectionText title='E-Commerce & English'>I have experience in designing marketing content for various platforms like Allegro and Facebook. I speak fluent English and know technical vocabulary allowing me to fully understand documentations.</SectionText>
            </TextAndImage>
          </section>
        </article> 
      </Element>
    </Grid>
  )
}

export default About
