import React from 'react'
import { Row, Col } from 'react-flexbox-grid'
import Image from '../image/image.component'

const TextAndImage = ({left, children, src, alt, cClass, reverseMobile}) => {
  return (
    <Row className={reverseMobile ? 'column-reverse' : ''}>
      <Col md={6} className={left ? 'text-right d-flex-col-center' : 'd-flex-col-center'}>
        {left ? children : <Image src={src} alt={alt} cClass={cClass} />}
      </Col>
      <Col md={6} className={left ? 'text-right d-flex-col-center' : 'p-l-section d-flex-col-center'}>
        {left ? <Image src={src} alt={alt} cClass={cClass} /> : children }
      </Col>
    </Row>
  )
}

export default TextAndImage
