import React from 'react'
import Link from '../../../common/link/link.component'
import './skill.styles.scss'

const Skill = ({skillClass, title, linkUrl, imageUrl}) => {
  return (
    <div className={`skill ${skillClass}`}>
      <Link outsideLink href={linkUrl} >
        <div className='title'>{title}</div>
        <div className='image'><img src={imageUrl}  alt={title} /></div>
      </Link>
    </div>
  )
}

export default Skill
