import React from 'react'
import { Element } from 'react-scroll'
import { Grid, Row, Col } from 'react-flexbox-grid'
import Image from '../../common/image/image.component'
import './hero.styles.scss'

const Hero = () => {
  return (
    <Grid className='p-y-section'>
      <Element name='home' className='hero'>
        <Row>
          <Col md={6} className='text'>
            <h1 className='slide-top'>I learn<br/>everyday.<span></span></h1>
            <p className='slide-top'>Hello, I'm a Junior Front End Developer.<br/>I live in Wrocław.</p>
          </Col>
          <Col md={6} className='image fade-in'>
            <Image src='images/people/hero.jpg' alt='hero'/>
          </Col>
        </Row>
      </Element>
    </Grid>
  )
}

export default Hero
