import React from 'react'
import './link.styles.scss'

const Link = ({ href, children, outsideLink, cClass }) => {
  return (
    // eslint-disable-next-line
    <a href={href} target='_blank' rel={outsideLink ? 'noopener noreferrer' : 'noreferrer'} className={cClass}>{children}</a>
  )
}

export default Link
