import React from 'react'
import { Link, Events, scroller } from 'react-scroll'
import { Grid } from 'react-flexbox-grid'
import './header.styles.scss'

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMenuOpen: false,
      className: 'notShadow',
    };
  }
  handleScroll() {
    if (document.documentElement.scrollTop > 70) {
      this.setState({
        className: 'withShadow'
      })
    } else {
      this.setState({
        className: 'notShadow'
      })
    }
  }

  componentDidMount() {
    Events.scrollEvent.register('begin', function () {
    });
    Events.scrollEvent.register('end', function () {
    });
    window.onscroll = () => this.handleScroll();
  }

  scrollTo() {
    scroller.scrollTo('scroll-to-element', {
      duration: 800,
      delay: 0,
      smooth: 'easeInOutQuart'
    })
  }

  componentWillUnmount() {
    Events.scrollEvent.remove('begin');
    Events.scrollEvent.remove('end');
  }

  toggleMenu() {
    this.setState({isMenuOpen: !this.state.isMenuOpen});
    document.querySelector('#hamburger').classList.toggle('open');
  }

  closeMenu() {
    if(this.state.isMenuOpen) {
      this.setState({isMenuOpen: false});
      document.querySelector('#hamburger').classList.toggle('open');
    }
  }

  render() {
    this.state.isMenuOpen ? document.body.style.overflow = 'hidden' : document.body.style.overflow = 'auto'
    return (
      <div className={this.state.isMenuOpen ? `header-background-dark header-sticky page-padding ${this.state.className}` : `page-padding header-sticky ${this.state.className}`}>
        <Grid>
          <header className='header'>
              <div className='logo slide-bottom'>
                <Link to='home' spy={true} offset={-150} smooth={true} duration={300} onClick={() => {this.closeMenu()}} >
                  Ingrid<div className='logo-sub'>Pruszyńska</div>
                </Link>
              </div>
              <nav className={this.state.isMenuOpen ? 'nav show-mobile' : 'nav'}>
                <Link onClick={() => {this.closeMenu()}} activeClass='active' to='home' spy={true} offset={-170} smooth={true} duration={300} className='link slide-bottom'>Home</Link>
                <Link onClick={() => {this.closeMenu()}} activeClass='active' to='about' spy={true} offset={-150} smooth={true} duration={300} className='link slide-bottom'>About</Link>
                <Link onClick={() => {this.closeMenu()}} activeClass='active' to='work' spy={true} offset={-150} smooth={true} duration={300} className='link slide-bottom'>Work</Link>
                <Link onClick={() => {this.closeMenu()}} activeClass='active' to='contact' spy={true} offset={-150} smooth={true} duration={300} className='link slide-bottom'>Contact</Link>
              </nav>    
              <div id="hamburger" className='slide-bottom' onClick={() => {this.toggleMenu()}}><span></span><span></span><span></span><span></span><span></span><span></span></div>
          </header>
        </Grid>
      </div>
    )
  }
}

export default Header
