import React from 'react'
import { Row, Col } from 'react-flexbox-grid'
import Video from '../video/video.component'

const TextAndVideo = ({left, children, src, alt, imgSrc, cClass, ...otherProps}) => {
  return (
    <Row {...otherProps}>
      <Col md={6} className={left ? 'text-right d-flex-col-center' : 'd-flex-col-center'}>
        {left ? children : <Video src={src} cClass={cClass} imgSrc={imgSrc} autoPlay muted loop/>}
      </Col>
      <Col md={6} className={left ? 'text-right d-flex-col-center' : 'p-l-section d-flex-col-center'}>
        {left ? <Video src={src} cClass={cClass} imgSrc={imgSrc} autoPlay muted loop/> : children }
      </Col>
    </Row>
  )
}

export default TextAndVideo
