import React from 'react'
import { Element } from 'react-scroll'
import { Grid } from 'react-flexbox-grid'
import Title from '../../common/title/title.component'
import Item from '../../common/item/item.component'
import './work.styles.scss'

const Work = () => {
  return (
    <Grid className='p-b-section'>
      <Element className='text-center' name='work'>
      <article className='p-y-sm-section'>
        <Title>Work</Title>
        <section className='items-container'>
          <Item src='images/work/jachty-pruszynski.jpg' alt='Jachty Pruszynski' href='https://jachtypruszynski.pl/' stack={['html', 'css', 'sass', 'javascript', 'react', 'redux', 'github', 'webpack', 'react-router', 'photoshop', 'jest']}>A commercial project for a chartering company with detailed descriptions of their products.</Item>
          <Item src='images/work/crwn-clothing.jpg' alt='Crwn Clothing' href='https://ilshae.github.io/crwn-clothing/' hrefGithub='https://github.com/Ilshae/crwn-clothing-src' stack={['html', 'css', 'sass', 'javascript', 'react', 'redux', 'github', 'react-router', 'firebase', 'styled-components', 'jest']}>Try out signing in with google or creating a new account and paying with test credit card.</Item>
          <Item src='images/work/hermes-travel-agency.jpg' alt='Hermes Travel Agency' href='https://hermes-travel-agency-app.herokuapp.com/' hrefGithub='https://github.com/Ilshae/react-travel-agency' stack={['html', 'css', 'sass', 'javascript', 'react', 'redux', 'github', 'webpack', 'react-router', 'jest']}>Dynamically sort trips by tags and duration or use a search bar. Ordered trips go to a JSON Server for later use.</Item>
          
          <Item src='images/work/mamma-mia-pizeria.jpg' alt='Mamma Mia Pizzeria' href='https://mamma-mia-italian-pizza.herokuapp.com/' hrefGithub='https://github.com/Ilshae/project-pizzeria' stack={['html', 'css', 'sass', 'javascript', 'react', 'redux', 'github', 'webpack', 'react-router']}>Pure Javascript app. Ordered products from cart go to a JSON Server. Live table booking.</Item>
          <Item src='images/work/tetris.jpg' alt='Tetris' href='https://ilshae.github.io/tetris/' hrefGithub='https://github.com/Ilshae/react-tetris' stack={['html', 'css', 'sass', 'javascript', 'react', 'github']}>Press start and use arrows to play.</Item>
        </section>
        </article> 
      </Element>
    </Grid>
  )
}

export default Work
