import React from 'react'
import Image from '../../common/image/image.component'
import Link from '../link/link.component'
import './item.styles.scss'

const Item = ({src, alt, children, stack, href, hrefGithub }) => {
  return (
    
      <article className='item-container'>
        <div><Link href={href}><Image src={src} alt={alt} /></Link></div>
        <div className='item-text'>
          <div className='text-left'>
            <h2><Link href={href}>{alt}</Link></h2>
            <p>{children}</p>
          </div>
          <footer className='item-footer'>
            <div>
            {stack.map(item => (
              <Image key={item} src={`images/skills/${item}.png`} cClass='item-image' alt={item} />
            ))}
            </div>
          {hrefGithub ? <span><Link href={hrefGithub} outsideLink cClass='section-link'>CHECK CODE</Link></span> : ''}
          </footer>
        </div>
      </article>
  )
}

export default Item
