import React from 'react'
import Image from '../image/image.component'

const Video = ({src, imgSrc, cClass, ...otherProps}) => {
  return (
    <video {...otherProps} className={cClass}>
      <source src={process.env.PUBLIC_URL + src} type='video/mp4' height="500px"/>
      <Image src={imgSrc} cClass={cClass} alt='video-not-supported'/>
    </video>
  )
}

export default Video
